  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 1rem;
}

.form-check {
  padding: 0;
}

label {
  padding: 1rem;
  margin: .5rem;
  border: solid 2px var(--cyan);
  font-size: 1rem;
  font-weight: bold;
}

.selected {
  color: white;
  background-color: var(--cyan);
}

input {
  position: absolute;
  opacity: 0;
}

