  position: relative;
  height: calc(100vh - 3rem);
  width: 100vw;
  overflow-x: hidden;
}

.tab-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  position: fixed;
  top: 3rem;
  height: 3rem;
  width: 100%;
  z-index: 1000;
  background-color: var(--farmos-green-dark);
  -webkit-box-shadow: -2px 0px 15px rgba(0, 0, 0, .5);
          box-shadow: -2px 0px 15px rgba(0, 0, 0, .5);
}

.tab {
  font-size: 1rem;
  line-height: 1.5rem;
  color: white;
  -webkit-transition: color .5s;
  transition: color .5s;
  -webkit-box-flex: 1;
      -ms-flex: 1 0 50%;
          flex: 1 0 50%;
  text-align: center;
  padding-top: 1rem;
}

.tab:not(.selected) {
  color: rgba(255, 255, 255, .5);
  -webkit-transition: color .5s;
  transition: color .5s;
}

.tab-indicator {
  position: fixed;
  top: calc(6rem - 2px);
  z-index: 1001;
  height: 2px;
  width: 50%;
  background-color: white;
}

.tab-indicator.first {
  -webkit-transition: left .5s;
  transition: left .5s;
  left: calc(25% - 25%);
}

.tab-indicator.second {
  -webkit-transition: left .5s;
  transition: left .5s;
  left: calc(75% - 25%);
}

.tab-content {
  position: absolute;
  width: 100vw;
  top: 3rem;
}

.tab-content.selected {
  -webkit-transition: left .5s;
  transition: left .5s;
  left: 0;
}

.tab-content.first:not(.selected) {
  -webkit-transition: left .5s;
  transition: left .5s;
  left: -100vw;
}

.tab-content.second:not(.selected) {
  -webkit-transition: left .5s;
  transition: left .5s;
  left: 100vw;
}

.reset-margin {
  margin: 0 0;
}

.preview {
  width: 100%;
  height: 100%;
}

.empty-slot {
  text-align: center;
  color: var(--gray);
}

.empty-slot button {
  margin: 0.5rem;
  color: var(--gray);
}

.remove-list-item {
  float: right;
}

#categories {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

#categories-label {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 100%;
          flex: 1 0 100%;
}

#categories p {
  font-style: italic;
}

.show-hide {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 100%;
          flex: 1 0 100%;
}

.show-hide p {
  font-size: 1rem;
}

.show-hide svg {
  vertical-align: bottom;
}

