
















































































































































.modal {
  display: block;
  padding-right: 15px;
  overflow-x: hidden;
  overflow-y: auto;
  opacity: 1;
}
.modal-filter {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #ccc;
  opacity: .5;
}
.modal-dialog {
  -webkit-transform: translate(0,0);
          transform: translate(0,0);
  width: auto;
  margin: 3rem;
  pointer-events: none;
}
@media (min-width: 576px) {
  .modal-dialog {
    max-width: 500px;
    margin: 1.75rem auto;
  }
}
