























































































































.search-results {
  position: absolute;
  z-index: 100;
  -webkit-box-shadow: 2px 2px 5px rgba(0,0,0,.5);
          box-shadow: 2px 2px 5px rgba(0,0,0,.5)
}
.search-results li {
  list-style: none;
}
.search-results li.is-active,
.search-results li:hover:not(.empty) {
  background-color: var(--cyan);
  color: white;
}
.invalid input {
  border-color: red;
}
.invalid label {
  color: red;
}
