
































.form-check {
  padding: 0;
}

input {
  position: absolute;
  left: -10000px;
  opacity: 0;
}

label {
  padding: 1rem;
  margin: .5rem;
  border: solid 2px var(--cyan);
  font-size: 1rem;
  font-weight: bold;
}

label.small {
  padding: .5rem;
  font-size: .875rem;
  margin: .25rem;
}

.selected {
  color: white;
  background-color: var(--cyan);
}
