





















































header {
  background-color: var(--farmos-green-dark);
  color: white;
  fill: white;
  border: none;
  height: 3rem;
}

header ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
  font-size: 1.25rem;
  line-height: 1.5rem;
}

.left-menu {
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}

.left-menu li {
  margin-right: .5rem;
}

.right-menu {
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}

.right-menu li {
  margin-left: .5rem;
}

#more-menu {
  position: absolute;
  top: 3px;
  right: 3px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  background-color: white;
  color: var(--gray-dark);
  -webkit-box-shadow: -1px 2px 5px rgba(0, 0, 0, .25);
          box-shadow: -1px 2px 5px rgba(0, 0, 0, .25);
}

#more-menu li {
  padding: 1rem 1rem;
  font-size: 1rem;
}

#more-menu a {
  color: var(--gray-dark);
}
