


















































































































































.close {
  position: absolute;
  top: 5px;
  right: 5px;
}

.logo {
  max-height: 1.5rem;
}

.drawer {
  position: fixed;
  top: 0;
  height: 100vh;
  width: 80vw;
  background-color: white;
  z-index: 2000;
  overflow-y: auto;
}

.modal-filter {
  position: fixed;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: #000;
  opacity: .25;
  z-index: 1500;
}

.drawer-enter, .drawer-leave-to {
  -webkit-transform: translateX(-80vw);
          transform: translateX(-80vw);
}

.filter-enter, .filter-leave-to {
  opacity: 0;
}

.drawer-enter-active, .drawer-leave-active,
.filter-enter-active, .filter-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

.drawer header {
  background-color: var(--farmos-green-dark);
  color: white;
  height: 9rem;
}

.arrow-back, .menu {
  fill: var(--gray-dark);
}

.arrow-back {
  fill: white;
  margin-top: 0.5rem;
}

main {
  margin-top: 3rem;
}

.drawer a {
  text-decoration: none;
  color: inherit;
}

input.toggle-check {
  position: relative;
  -webkit-appearance: none;
  outline: none;
  width: 1.6667rem;
  height: 1rem;
  background-color: #fff;
  border: 1px solid #D9DADC;
  border-radius: 1.6667rem;
  -webkit-box-shadow: inset -0.6667rem 0 0 0 #fff;
          box-shadow: inset -0.6667rem 0 0 0 #fff;
}

input.toggle-check:after {
  content: "";
  position: absolute;
  top: 1px;
  left: 1px;
  background: transparent;
  width: 0.8667rem;
  height: 0.8667rem;
  border-radius: 50%;
  -webkit-box-shadow: 1px 0px 2px rgba(0,0,0,0.2);
          box-shadow: 1px 0px 2px rgba(0,0,0,0.2);
}

input.toggle-check:checked {
  -webkit-box-shadow: inset 0.6667rem 0 0 0 var(--cyan);
          box-shadow: inset 0.6667rem 0 0 0 var(--cyan);
  border-color: var(--cyan);
}

input.toggle-check:checked:after {
  left: 0.6667rem;
  -webkit-box-shadow: -1px 0px 2px rgba(0,0,0,0.05);
          box-shadow: -1px 0px 2px rgba(0,0,0,0.05);
}

